@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap');

html, body {
  font-family: 'Montserrat';
  font-weight: 300;
}

p {
   text-align: center;
}

.header {
  width: 100%;
  text-align: right;
  position: relative;
  
  .logo {
    position: absolute;
    top: 0;
    left: 0;
    font-size: 32px;
    font-weight: 100;
    padding: 26px;
  }
}

.link {
  display: inline-block;
  padding: 15px 0;
  margin: 15px 25px;
  position: relative;
  text-decoration: none;
  text-transform: uppercase;
  color: #0f0f0f;
  font-weight: 800;
  font-size: 24px;
  
  .bar {
    width: 100%;
    display: inline-block;
    border-bottom: 2px solid black;
    position: absolute;
    bottom: 0;
    left: 0;
    opacity: 0;
    transition: all 300ms;
  }
  
  &:hover {
    .bar {
      opacity: 1;
      bottom: 5px;
    }
  }
}